<template>
  <div class="ServiceAdminEdit">
    <h2>{{handleType=="report"?"举报信息":"反馈信息 "}}</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">礼物名称：</div>
        <el-input v-model="form.name"></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">礼物价格：</div>
        <el-input v-model="form.name"></el-input>
      </el-col>
    </el-row>
    <el-row :gutter="50">
      <el-col :span="8">
        <div class="name">礼物类型：</div>
        <el-input v-model="form.hometownCity"></el-input>
      </el-col>
      <el-col :span="8">
        <div class="name">排序：</div>
        <el-input v-model="form.schoolName"></el-input>
      </el-col>
    </el-row>
    <h2>反馈资料</h2>
    <el-divider></el-divider>
    <el-row :gutter="50">
      <el-col :span="8" v-if="handleType=='report'">
        <div class="name">举报理由：</div>
        <el-input v-model="form.name" disabled></el-input>
      </el-col>
    </el-row>
    <!-- 图片上传 -->
    <div class="imgupload">
      <div class="name">礼物图标：</div>
      <div>
        <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList"
          list-type="picture"
        >
          <el-button size="small" type="primary">{{fileList.length > 0 ? "重新上传" : "点击上传"}}</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </div>
      <div class="name">礼物GIF图标：</div>
      <div>
        <el-upload
          class="upload-demo"
          action="#"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList"
          list-type="picture"
        >
          <el-button size="small" type="primary">{{fileList.length > 0 ? "重新上传" : "点击上传"}}</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </div>
    </div>

    <div class="bigGift">
      <div class="name">大礼物资源动画:</div>
      <el-upload
        class="upload-demo"
        drag
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          将文件拖到此处，或
          <em>点击上传</em>
        </div>
      </el-upload>
    </div>

    <el-row :gutter="50">
      <el-col :span="8">
        <el-button @click="close">关闭</el-button>
        <el-button @click="preservation">保存</el-button>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //处理类型 ， 用户判断是举报还是反馈
      handleType: this.$route.query.type,
      fileList: [
        {
          name: "food.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        }
      ],
      srcList: [
        "https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1416536552,3950553019&fm=26&gp=0.jpg",
        "https://dss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3521319392,1160740190&fm=26&gp=0.jpg",
        "https://dss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1068485212,3292662520&fm=111&gp=0.jpg"
      ],
      //表单数据
      form: {
        name: "王大拿", //昵称
        sex: 1, //性别
        hometownCity: "", //家乡城市
        schoolName: "", //学校
        birthday: "", //出生日期
        autograph: "" //个性签名
      }
    };
  },
  methods: {
    //删除图片
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    //添加图片
    handlePreview(file) {
      console.log(file);
    },
    // 保存
    preservation() {
      this.$confirm("是否保存？", {
        confirmButtonText: "保存",
        cancelButtonText: "取消"
      })
        .then(() => {
          this.$message({
            message: "保存成功",
            type: "success"
          });
          this.$router.go(-1);
        })
        .catch(() => {});
    },
    //关闭
    close() {
      this.$confirm("是否关闭？", {
        confirmButtonText: "关闭",
        cancelButtonText: "取消"
      }).then(() => {
        this.$router.go(-1);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.ServiceAdminEdit {
  padding-bottom: 200px;
}
h2 {
  padding-top: 30px;
}
.imgupload {
  display: flex;
  > div:nth-child(2n) {
    width: 30%;
    display: flex;
    .el-upload__tip {
      padding: 15px 0;
    }
  }
}
.bigGift {
  padding: 30px 0;
  display: flex;
}
</style>